@import "./variables/variables.sass";
@import "./variables/mixins.sass";

.MuiPaper-root.transaction:last-child {
  margin-bottom: 15vh; }
.transaction-card {
  text-align: left;
  line-height: 9px;
  box-shadow: 0 2px 2px rgba(0,0,0,0.25);
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 5%;
  margin-bottom: 1%;
  font-family: 'Lato', sans-serif;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  background-color: #e57373;
  color: white; }

.transaction-details {
  display: flex;
  justify-content: space-around; }
