@import "./variables/variables.sass";
@import "./variables/mixins.sass";

.onboarding {
  text-align: center;
  padding: 4vh 0;
  .avatar-pig {
    height: 162px;
    margin-top: -3vh;
    margin-bottom: 5vh; }
  .swipe-card {
    border: 1px solid $bg-color;
    border-radius: 20px;
    background-color: white;
    // box-shadow: 0 0 15px rgba(0,0,0,0.25)
    height: 80vh;
    margin: 2vh;
    text-align: center;
 }    // margin: 10vh 0

  .onboarding-stepper {
    background-color: $bg-color;
    width: 20%;
    margin: 13vh 0 0 35vw; }
  .title {
    margin: 5vh 0;
    font-family: $title-font;
    font-weight: 300;
    font-size: 33px;
    color: #5550ef;
    text-align: center;
    margin: 10vh 0; }
  .onboarding1-content {
    font-family: $title-font;
    color: #4e342e;
    font-size: 20px;
    margin-left: 2vh;
    margin-right: 2vh; }
  .avatar-budget {
    margin-top: 5vh;
    margin-bottom: 2vh; }
  .avatar-pie {
    margin-top: 4vh;
    margin-bottom: 2vh; }
  .icons-category {
    display: flex;
    justify-content: space-around;
    margin-top: 4vh;
    margin-bottom: 2vh;
    color: #01579b; }
  .specific-icon {
    font-size: 50px; }
  .transaction-camera {
    height: 112px;
    margin-top: 7vh;
    margin-bottom: 3vh; }
  .badges-final {
    height: 223px;
    margin-top: -8vh;
    margin-bottom: 3vh; }
  .onboarding4-content {
    font-family: $title-font;
    color: #4e342e;
    font-size: 20px;
    margin-top: 2vh;
    padding: 1vh; }
  .onboarding-button {
    @include button; } }
